@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Poppins', sans-serif;
}

*{
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;

  font-family:'Poppins',sans-serif;
}
.navbar{
 height: 3rem;
 background-color: white;
 display: flex;
 justify-content: start;
 padding: 10px;
 border-bottom: 2px solid #ebebec;
 width: 100%;
}
.title{
    font-weight: bold;
    font-size: x-large;
    color: #0d3253;
    font-family: Poppins;
    width: 92%;
}
.menuIcon{
    align-self: flex-end;
    width: 8%;
}
.menulist{
    display: none;
}
@media screen and (min-width:786px){
    .menuIcon{
        display: none;
    }
    .menulist{
        width: 50%;
        display: inline;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;

    

    }
    .menuItem:hover{
       color: #5cc5d5;
       border-bottom: 2px solid #5cc5d5;
    }
    .title{
        width: 50%;
    }

}

.home{
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 80vh;
    padding: 2%;

}
.home_left{
    width: 50%;
    /* background-color: #5cc5dc; */
    height: 80vh;
    display: flex;
    justify-content: center;
}
.home_right{
    width: 50%;
    /* background-color: #5cc5dc; */
    height: 80vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-image: url(/Rohan2596.github.io/saptalabz-offical/static/media/background.27fe5be6.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

}
.home_image{
    width:100%;
    height: 80vh;
}
.tag{
    background-color: #5cc5d5;
width: -webkit-fit-content;
width: -moz-fit-content;
width: fit-content;
padding-left: 2%;
padding-right: 2%;
padding-top: 1%;
padding-bottom: 1%;
border-radius: 1%;
font-size:medium;
color: white;
-webkit-animation-duration: 1.5s;
        animation-duration: 1.5s; }
.tagline{
    font-weight: bolder;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size:xx-large;
}
.description{
    font-size: medium;
}
@media screen and (max-width:786px){

    .home{
        display: flex;
        flex-direction:column-reverse;
        /* height: 80vh; */
    }
    .home_left{
        width: 100%;
        /* background-color: #5cc5dc; */
        height: 50vh;
    }
    .home_image{
        width:100%;
        height: 50vh;
        padding: 4%;
    }
    .home_right{
        width: 100%;
        /* background-color: #5cc5dc; */
        height: 50vh;
    }
    .tag{
        background-color: #5cc5d5;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        padding-left: 2%;
        padding-right: 2%;
        padding-top: 1%;
        padding-bottom: 1%;
        border-radius: 1%;
        font-size:x-small;
    }

    .tagline{
        font-weight: bolder;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        font-size: medium;
    }
    .description{
        font-size: smaller;
    }
}
@-webkit-keyframes tag{
    0% {
        background-color: white;
      }
      100% {
        background-color: #5cc5d5;
      }
}
@keyframes tag{
    0% {
        background-color: white;
      }
      100% {
        background-color: #5cc5d5;
      }
}
.aboutUs{
    width: 100%;
    height: 80vh;
    background-color: white;
    display: flex;
    flex-direction: row;
}
.aboutUs-left{
    width: 50%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2%;
    background-image: url(/Rohan2596.github.io/saptalabz-offical/static/media/background.27fe5be6.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}
.aboutUs_title{
    font-weight: bolder;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: xx-large;
}
.aboutUs-description{
    font-size: medium;
}
.aboutUs-right{
    width: 50%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2%;
}
.aboutus-image{
    width: 100%;
}
.team{
    display: flex;
    flex-direction: column;
    justify-content: center space-around;
    
}
.our_team{
    font-size: xx-large;
    font-weight: bolder;
    display: flex;
    justify-content: center;
}
.team_members{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-self: center;
    text-align: center;
    background-image: url(/Rohan2596.github.io/saptalabz-offical/static/media/background.27fe5be6.png);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    
}
.team_avatar{
    vertical-align: middle;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    align-self: center;   
}
.team_info{
    font-size: larger;
}
@media screen and (max-width:786px){
    .aboutUs{
       
        display: flex;
        flex-direction: column;
    }
    .aboutUs-left{
        width: 100%;
        height: 50vh;
        padding: 4%;
    
    }
    .aboutUs_title{
       
        font-size: large;
    }
    .aboutUs-description{
        font-size: smaller;
    }
    .aboutUs-right{
        width: 100%;
        height: 50vh;
       
        padding: 2%;
    }
    .aboutus-image{
        width: 100%;
    } 
    .our_team{
        font-size: x-large;
   
    }
}
